import { useState, useEffect } from 'react';
import { VELLORE_CRIME_HOTSPOTS } from '../data/velloreRealData';

export interface Zone {
    id: number;
    location: string;
    lat: number;
    lng: number;
    severity: 'HIGH' | 'MODERATE' | 'LOW';
    type: string;
    description: string;
    radius?: number;
    active_hours?: string;
}

export function useZones() {
    const [zones, setZones] = useState<Zone[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        // Convert real Vellore crime hotspots to zone format
        const realZones: Zone[] = VELLORE_CRIME_HOTSPOTS.map((hotspot, index) => ({
            id: index + 1,
            location: hotspot.location,
            lat: hotspot.coordinates.lat,
            lng: hotspot.coordinates.lng,
            severity: hotspot.severity,
            type: hotspot.crimeTypes[0].toLowerCase().replace(/ /g, '_'),
            description: hotspot.description,
            radius: hotspot.radius,
            active_hours: hotspot.activeHours
        }));

        setZones(realZones);
        setLoading(false);
    }, []);

    return { zones, loading };
}
